import TextScaled from '@/components/Common/TextScaled';
import { icons, images } from '@/constants';
import Image from 'next/image';
import { useRouter } from 'next/navigation';
import { StaticImageData } from 'next/image';

interface FoodItem {
    id: string;
    name: string;
    image: StaticImageData | string;
}

interface FoodGridProps {
    featuredRecipesData?: FoodItem[];
}

const FoodGrid = ({ featuredRecipesData }: FoodGridProps) => {
    const router = useRouter();
    const searchedDishesData = [
        { id: '1', name: 'Tm hong  nh kim', image: images.featuredFood1 },
        { id: '2', name: 'Tm nng st tiu en', image: images.featuredFood2 },
        { id: '3', name: 'Lu hi sn vi nc st tm', image: images.featuredFood3 },
        { id: '4', name: 'S huyt rau mi', image: images.featuredFood4 },
        { id: '5', name: 'Tm st mm t', image: images.featuredFood5 },
        { id: '6', name: 'C nng l chui', image: images.featuredFood6 },
        { id: '7', name: 'Tht b xo rau c', image: images.featuredFood7 },
        { id: '8', name: 'Trng chin tht bm', image: images.featuredFood8 },
        { id: '9', name: 'Ph b truyn thng', image: images.featuredFood1 },
        { id: '10', name: 'Bn ch H Ni', image: images.featuredFood2 },
        { id: '11', name: 'Bnh m tht nng', image: images.featuredFood3 },
        { id: '12', name: 'Gi cun tm tht', image: images.featuredFood4 },
        { id: '13', name: 'Cm tm sn nng', image: images.featuredFood5 },
        { id: '14', name: 'Bnh xo min Ty', image: images.featuredFood6 },
        { id: '15', name: 'Ch gi nem rn', image: images.featuredFood7 },
        { id: '16', name: 'Bn b Hu', image: images.featuredFood8 },
        { id: '17', name: 'M Qung tm tht', image: images.featuredFood1 },
        { id: '18', name: 'Cao lu Hi An', image: images.featuredFood2 },
        { id: '19', name: 'Bn riu cua', image: images.featuredFood3 },
        { id: '20', name: 'Cho lng tit canh', image: images.featuredFood4 },
        { id: '21', name: 'Bnh canh cua', image: images.featuredFood5 },
        { id: '22', name: 'H tiu Nam Vang', image: images.featuredFood6 },
        { id: '23', name: 'Bnh cun Thanh Tr', image: images.featuredFood7 },
        { id: '24', name: 'Xi xo H Ni', image: images.featuredFood8 },
        { id: '25', name: 'Bnh c nng', image: images.featuredFood1 },
        { id: '26', name: 'Ch tri nc', image: images.featuredFood2 },
        { id: '27', name: 'Bnh trng nng  Lt', image: images.featuredFood3 },
        { id: '28', name: 'Bnh m xu mi', image: images.featuredFood4 },
        { id: '29', name: 'Bnh t tht nng', image: images.featuredFood5 },
        { id: '30', name: 'Bnh bo Hu', image: images.featuredFood6 },
    ];

    const data = featuredRecipesData || searchedDishesData;
    
    // Mng cc chiu cao khc nhau cho images (n v: rem)
    const imageHeights = [12, 16, 14, 18, 15, 13, 17, 12, 16, 14, 15, 13];
    
    // Chia items thnh 6 ct
    const columns: (typeof data)[] = [[], [], [], [], [], []];
    data.forEach((item, index) => {
        columns[index % 6].push(item);
    });
    
    const renderItem = (item: FoodItem, index: number) => {
        const height = imageHeights[index % imageHeights.length];
        return (
            <button
                onClick={() => router.push('/food-detail')}
                className="group w-full bg-white rounded-lg gap-1 flex flex-col items-start justify-start shadow-md transition-all duration-300 ease-in-out hover:shadow-xl cursor-pointer"
            >
                <div 
                    className="relative w-full overflow-hidden rounded-t-lg"
                    style={{ height: `${height}rem` }}
                >
                    <Image
                        src={item.image}
                        alt={item.name}
                        fill
                        className="object-cover transition-transform duration-500 ease-in-out group-hover:scale-110"
                    />
                </div>
                <div
                    className="w-full p-2 pt-1 rounded-lg gap-1 flex flex-row items-center justify-between"
                >
                    <TextScaled
                        size="sm"
                        className="flex-1 font-semibold text-black text-start line-clamp-1 transition-colors duration-300 group-hover:text-orange-500"
                    >
                        {item.name}
                    </TextScaled>
                    <Image 
                        src={icons.threeDotsIcon} 
                        alt="more" 
                        className="w-5 h-5 transition-all duration-300 ease-in-out group-hover:scale-110 group-hover:opacity-80" 
                    />
                </div>
            </button>
        );
    };
    
    return (
            <div
                className="pb-[112px] w-full flex"
                style={{ gap: '16px' }}
            >
                {columns.map((column, colIndex) => (
                    <div
                        key={colIndex}
                        className="flex-1 flex flex-col"
                        style={{ width: '0' }}
                    >
                        {column.map((item, itemIndex) => {
                            // Tnh index thc t ca item trong data gc
                            const originalIndex = colIndex + itemIndex * 6;
                            const isLastItem = itemIndex === column.length - 1;
                            return (
                                <div
                                    key={item.id}
                                    style={{ marginBottom: isLastItem ? '0' : '16px' }}
                                >
                                    {renderItem(item, originalIndex)}
                                </div>
                            );
                        })}
                    </div>
                ))}
            </div>
    );
};

export default FoodGrid;
